<script>
import { ref, computed, watchEffect,onMounted } from "vue";
import { useLocalStorage } from "@vueuse/core";
import axios from "axios"; // 使用axios发送请求
import {
  Connection,
  PublicKey,
  Keypair,
  clusterApiUrl,
  SystemProgram,
} from "@solana/web3.js";
import { AnchorProvider, Program } from "@project-serum/anchor";
import { WalletMultiButton, useAnchorWallet } from "../src";
import idl from "./idl.json";

const programID = new PublicKey(idl.metadata.address);
const preflightCommitment = "processed";

export default {
  components: {
    WalletMultiButton,
  },
  setup() {
    const dark = ref(false);
    const wallet = useAnchorWallet();
    const isBound = ref(false); // 控制成功弹框显示
    const bindError = ref(null); // 控制错误弹框显示
    const uid = ref(null);

    const connection = new Connection(
      clusterApiUrl("devnet"),
      preflightCommitment
    );

    onMounted(() => {
      const urlParams = new URLSearchParams(window.location.search);
      uid.value = urlParams.get("uid"); // 从 URL 获取 uid
    });

    watchEffect(async () => {
      if (wallet.value?.publicKey) {
        const address = wallet.value.publicKey.toBase58();
        try {
          const response = await axios.post("https://api.corgia.vip/api/Finance/sol/bind", {
            tgId: uid.value,
            address: address,
          });

          // 检查响应状态
          if (response.status === 200 && response.data.code == 1) {
            isBound.value = true; // 显示成功弹框
          } else {
            bindError.value = response.data.msg || "绑定失败，请重试。2";
          }
        } catch (error) {
          console.log("绑定失败", error);
        }
      }
    });

    const provider = computed(() => {
      if (!wallet.value) return;
      return new AnchorProvider(connection, wallet.value, {
        preflightCommitment,
      });
    });
    const program = computed(() => {
      if (!provider.value) return;
      return new Program(idl, programID, provider.value);
    });


    const walletAddressShort = computed(() => {
      const address = wallet.value?.publicKey?.toBase58();
      if (!address) return 'Not connected';
      return address.length > 10 
        ? `${address.slice(0, 5)}...${address.slice(-5)}` 
        : address;
    });

    return {
      dark,
      walletAddressShort, // 这里返回简短的钱包地址
      isBound,
      bindError
    };
  },
};
</script>

<template>
  <div
    class="h-screen w-screen flex"
    :class="dark ? 'bg-gray-900 text-gray-100' : 'bg-gray-100 text-gray-700'"
  >
    <!-- Top-Right Corner. -->
    <div class="absolute top-0 right-0 p-8 flex space-x-8">
      <!-- Dark Button. -->
      <button
        @click="dark = !dark"
        class="rounded-full p-3"
        :class="
          dark
            ? 'bg-white/10 hover:bg-white/20 text-gray-200'
            : 'bg-black/10 hover:bg-black/20 text-gray-600'
        "
      >
        <svg
          v-if="dark"
          xmlns="http://www.w3.org/2000/svg"
          class="h-6 w-6"
          fill="none"
          viewBox="0 0 24 24"
          stroke="currentColor"
        >
          <path
            stroke-linecap="round"
            stroke-linejoin="round"
            stroke-width="2"
            d="M12 3v1m0 16v1m9-9h-1M4 12H3m15.364 6.364l-.707-.707M6.343 6.343l-.707-.707m12.728 0l-.707.707M6.343 17.657l-.707.707M16 12a4 4 0 11-8 0 4 4 0 018 0z"
          />
        </svg>
        <svg
          v-else
          xmlns="http://www.w3.org/2000/svg"
          class="h-6 w-6"
          fill="none"
          viewBox="0 0 24 24"
          stroke="currentColor"
        >
          <path
            stroke-linecap="round"
            stroke-linejoin="round"
            stroke-width="2"
            d="M20.354 15.354A9 9 0 018.646 3.646 9.003 9.003 0 0012 21a9.003 9.003 0 008.354-5.646z"
          />
        </svg>
      </button>

      <!-- Solana Wallets Vue. -->
      <wallet-multi-button :dark="dark"></wallet-multi-button>
    </div>

    <!-- Centered. -->
    <div class="m-auto w-full max-w-md p-8">
      <div
        class="shadow-xl rounded-xl"
        :class="dark ? 'bg-gray-700' : 'bg-white'"
      >
        <div class="p-8 text-center">
          <p
            class="uppercase text-xs tracking-widest text-gray-400 font-semibold"
          >
            Wallet Address
          </p>
          <p
            class="font-bold text-5xl mt-2"
            :class="dark ? 'text-white' : 'text-gray-800'"
            v-text="walletAddressShort"
          ></p>
        </div>  
      </div>
    </div>
  </div>

   <!-- 成功弹框 -->
   <div>
    <!-- 成功弹框 -->
    <div v-if="isBound" class="popup-overlay">
      <div class="success-popup">
        <p>SUCCESS!!</p>
        <a href="https://t.me/corgiawebbot" class="return-button">Back Game</a>
      </div>
    </div>

    <!-- 失败弹框 -->
    <div v-if="bindError" class="popup-overlay">
      <div class="error-popup">
        <p>{{ bindError }}</p>
        <button @click="bindError = null" class="close-button">关闭</button>
      </div>
    </div>
  </div>
</template>

